<template>
  <div @click.stop='action'>
    <div :class='wrapperClass' :width='width' :height='height'>
      <video
        id='video'
        :class='videoClass'
        :autoplay='autoplay'
        :fullscreen='fullscreen'
        v-show='!image.base64'
        :controls='controls'
      ></video>
      <canvas
        id="canvas"
        :class='canvasClass'
        v-show='image.base64'
        :width='constraints.video.width.ideal'
        :height='constraints.video.height.ideal'
      ></canvas>
    </div>

    <div v-if='slots.length'>
      <div v-for="(slot, key) in slots" :key='key'
        v-show="slot.show"
        :name='slot.name'
        :class="slot.class">
        {{slot.class ? '' : slot.name}}
      </div>
    </div>
    <div v-else>
      <slot name="capture"></slot>
      <slot name="record"></slot>
      <slot name="pause"></slot>
      <slot name="resume"></slot>
      <slot name="save"></slot>
      <slot name="stop"></slot>
      <slot name="flip"></slot>
      <slot name="reset"></slot>
      <slot name="close"></slot>
      <slot name="fullscreen"></slot>
    </div>
  </div>
</template>

<script>


import imageTypes from '../utils/imageTypes';
import mediaTypes from '../utils/mediaTypes';

export default {
  name: 'Camera',

  props: {
    /**
     * If true, the browser asks permission to access the camera
     * @type {Boolean}
     */
    requestAccess: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * If true, the video recording will contain audio track
     * @type {Boolean}
     */
    audio: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * Pass the image type you need when saving images from the camera
     * Supported formats (jpeg, jpg, png, gif)
     * @type {String}
     */
    imageType: {
      type: String,
      required: false,
      default: 'image/png',
      validator: type => imageTypes.map(x => x.mime).includes(type),
    },


    /**
     * The video format type to download
     * @type {String}
     */
    mediaType: {
      type: String,
      required: false,
      default: 'video/webm',
      validator: type => mediaTypes.map(x => x.mime).includes(type),
    },


    /**
     * Canvas width of the camera view
     * @type {Number}
     */
    width: {
      type: Number,
      required: false,
      default: 400,
    },


    /**
     * Canvas Height of the camera view
     * @type {Number}
     */
    height: {
      type: Number,
      required: false,
      default: 500,
    },


    /**
     * By default the browser adds controls (play, pause)
     * to the video object. You can show/hide it by passing a boolean
     * @type {Boolean}
     */
    controls: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * Customized slots to pass as object, ex:
     * :slots=[{name: 'snap', show: true, calss: '}]
     * @type {Array}
     * @todo: check properly if slots contain all props
     */
    slots: {
      type: Array,
      required: false,
      default() {
        return [];
      },
    },


    /**
     * autoplay video
     * @type {Boolean}
     */
    autoplay: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * Show camera in fullscreent
     * @type {Boolean}
     */
    fullscreen: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * class for the main div wrapper
     * @type {String}
     */
    wrapperClass: {
      type: String,
      required: false,
      default: 'vc-wrapper',
    },


    /**
     * css class the <video>
     * @type {String}
     */
    videoClass: {
      type: String,
      required: false,
      default: 'vc-video',
    },


    /**
     * css class for <canvas>
     * @type {String}
     */
    canvasClass: {
      type: String,
      required: false,
      default: 'vc-canvas',
    },


    /**
     * Name of the downloabled content (picture/video)
     * @type {String}
     */
    name: {
      type: String,
      required: false,
      default: () => `vc-${Math.floor(Date.now() + Math.random())}`,
      validator: val => val.trim().length,
    },


    /**
     * The MediaDevices.getUserMedia() api requires
     * a constraint to access user's device, you can either
     * pass one or the default will be used
     * MDN docs: https://goo.gl/AV7qvg
     * @type {Object}
     * @todo make a func to check object structure
     */
    constraints: {
      type: Object,
      required: false,
      default() {
        return {
          audio: true,
          video: {
            width: { min: 200, ideal: 500, max: 1920 },
            height: { min: 300, ideal: 600, max: 1080 },
            facingMode: 'user',
          },
        };
      }
    },


    /**
     * An option to access the available camera
     * on smart phones, 'user' means front and
     * 'environment' means the back
     * @type {String}
     */
    facingMode: {
      type: String,
      required: false,
      default: 'user',
      validator: v => v === 'user' || v === 'environment',
    },
  },


  data() {
    return {
      image: {
        base64: '',
      },

      media: {
        recorder: {},
        chunk: [],
        blob: null,
        stream: null,
      },
    };
  },

  watch: {
    /**
     * Request or Stop access to camera
     * @param {Boolean} true to request access
     */
    requestAccess(state) {
      if (state) {
        this.access();
      }
    },


    /**
     * Access camera in Fullscreen mode
     * @param {Boolean}
     */
    fullscreen(state) {
      if (state) {
        this.requestFullscreen();
      }
    },
  },

  methods: {
    /**
     * Toggle fullscreen to use camera in full screen mode
     */
    requestFullscreen() {
      document.getElementById('video').requestFullscreen();
    },


    /**
     * On mobile phone or on a device where there is more than one camera
     * you can switch between using this method. 
     * user: front facing camera
     * environment: back facing camera
     */
    changeCameraFace() {
      this.constraints.video.facingMode === 'user' ? 'environment' : 'user';
      this.access();
    },


    /**
     * Creates a constraint based on given or default list of options
     * passed via props.
     */
    mediaConstraints() {

      const {
        audio,
        width,
        height,
        facingMode,
        constraints
      } = this;

      constraints.audio = { audio };
      constraints.video.width = width ? { ideal: width } : constraints.video.width.ideal;
      constraints.video.height = height ? { ideal: height } : constraints.video.height.ideal;
      constraints.video.facingMode = facingMode || constraints.video.facingMode;

      return constraints;
    },


    /**
     * Stops recording and clears media tracks and media tracks
     */
    reset() {
      this.stop();
      this.image = { base64: '' };
      this.media = {
        recorder: {},
        chunk: [],
        blob: null,
        stream: null,
      };
    },



    /**
     * Resume recording if media state is currently paused
     */
    resume() {
      var recorder = this.media.recorder; 

      if(recorder && recorder.state === "paused") {
        recorder.resume() 
        this.$emit('media:resume');
      }
    },


    /**
     * Pause recording if media state is currently recording
     */
    pause() {
      var recorder = this.media.recorder; 

      if(recorder && recorder.state === "recording") {
        recorder.pause() 
        this.$emit('media:paused');
      }
    },


    /**
     * Download image
     */
    saveImage() {
      const link = document.createElement('a');
      // todo: code might need improvement
      link.download = `${this.name}.${this.imageType.split('/')[1]}`;
      link.href = this.image.base64;
      link.click();
    },


    /**
     * Takes a picture and stores the data in image.base64
     */
    capture() {
      const {
        width,
        height,
      } = this.constraints.video;

      const canvas = document.getElementById('canvas');
      const video = document.getElementById('video');

      canvas.getContext('2d').drawImage(
        video,
        0,
        0,
        width.ideal,
        height.ideal,
      );

      // this.image.isCaptured = true;
      this.image.base64 = canvas.toDataURL();
      this.$emit('image:captured', this.image.base64);
    },


    /**
     * Request camera access
     */
    access() {
      const video = document.getElementById('video');

      navigator.mediaDevices.getUserMedia(
        this.mediaConstraints(),
      ).then(
        (stream) => {
          this.$emit('access:granted');

          this.media.stream = stream;

          if ('srcObject' in video) {
            video.srcObject = stream;
          } else {
            video.src = window.URL.createObjectURL(stream);
          }

          video.onloadedmetadata = function () {
            video.play();
          };
        },
      ).catch((error) => {
        this.$emit('access:denied', error);
      });
    },


    /**
     * Records video and audio (if audio is set to true)
     * @todo: check browser compatability
     */
    record() {
      navigator.mediaDevices.getUserMedia(
        this.mediaConstraints(),
      ).then(
        (stream) => {
          this.$emit('media:recording');
          const self = this;
          this.media.recorder = new MediaRecorder(
            stream, { mimeType: this.mediaType },
          );
          this.media.recorder.start(10);
          this.media.recorder.ondataavailable = function (e) {
            self.media.chunk.push(e.data);
          };
        },
      );
    },


    /**
     * stop/end recording
     * @todo: check if recorder has data before calling .stop()
     */
    stop() {
      const { media, mimeType } = this; 
      if(media.recorder.state == "recording") {
        media.recorder.stop() 
        media.blob = new Blob(media.chunk, {type: mimeType})
        this.$emit('status:stop')
      }
    },


    /**
     * Download video or audio or both
     */
    saveMedia() {
      const { name, media } = this;

      if (!media.blob) {
        /** @todo [maybe] throw an error? */
        return;
      }

      const audio = document.createElement('audio');
      const link = document.createElement('a');
      const src = window.URL.createObjectURL(
        this.media.blob,
      );

      audio.src = src;

      link.style.display = 'none';
      link.download = `${name}.webm`;
      link.href = src;
      document.body.appendChild(link);
      link.click();
      this.reset()
    },

    /**
     * Gets the name of the slot clicked and execute
     * related method
     * @param {Object} - DOM props of the clicked slot
     */
    action(event) {
      // don't run functions if access is not accepted
      // @todo remove when "smart buttons" feature is added
      if (!this.requestAccess) {
        return;
      }

      // let action = event.toElement.outerText
      // @todo: Have to find a better solution than this
      const action = event.path[0].attributes[1].nodeValue || '';

      switch (action) {
        case 'capture':
          this.capture();
          break;

        case 'record':
          this.record();
          break;

        case 'pause':
          this.pause();
          break;

        case 'resume':
          this.resume();
          break;

        case 'stop':
          this.stop();
          break;

        case 'save':
          if (this.image.base64) {
            this.saveImage();
          } else if (this.media.chunk) {
            this.saveMedia();
          }
          break;

        case 'reset':
          this.reset();
          break;

        case 'fullscreen':
          this.requestFullscreen();
          break;

        case 'flip':
          this.changeCameraFace();
          break;

        default:
          break;
      }
    },
  },

  created() {},

};
</script>

