<template>
  <div @click.stop='action'>

    <audio id="audio" class="vc_audio"></audio>

    <div v-if='slots.length'>
      <div v-for="(slot, key) in slots" :key='key'
        v-show="slot.show"
        :name='slot.name'
        :class="slot.class">
        {{slot.class ? '' : slot.name}}
      </div>
    </div>
    <div v-else>
      <slot name="record"></slot>
      <slot name="pause"></slot>
      <slot name="resume"></slot>
      <slot name="stop"></slot>
      <slot name="save"></slot>
      <slot name="reset"></slot>
    </div>

  </div>
</template>

<script>


import mediaTypes from '../utils/mediaTypes';
import { setInterval, setTimeout } from 'timers';

export default {
  name: 'Microphone',

  props: {
    /**
     * Pass true to request access user device (microphone)
     * or false to stop the access
     * @type {Boolean}
     */
    requestAccess: {
      type: Boolean,
      required: false,
      default: false,
    },


    /**
     * The MediaDevices.getUserMedia() api requires
     * a constraint to access user's device
     * @see https://developer.mozilla.org/en-US/docs/Web/API/MediaDevices/getUserMedia
     *
     * @type {Object}
     * @todo validate if given constraint exists in MediaDevices' API
     */
    constraints: {
      type: Object,
      required: false,
      default() {
        return {
          constraints: { audio: true },
        };
      },
    },


    /**
     * Used to give a custom name for the dawnloaded audio file
     * @type {String}
     */
    name: {
      type: String,
      required: false,
      default: () => `vc-${Math.floor(Date.now() + Math.random())}`,
      validator: val => val.trim().length,
    },


    /**
     * Specify the audio bit rate for the media recorder
     * Higher bits per second mean better audio quality
     *
     * @see https://developer.mozilla.org/en-US/docs/Web/API/media.recorder/media.recorder#Example
     * @type {Object}
     */
    bitRate: {
      type: Number,
      required: false,
      default: () => 128000,
    },


    /**
     * whether or not echo cancellation is enabled on an audio track
     * @type {Boolean}
     */
    echoCancellation: {
      type: Boolean,
      required: false,
      default: true,
    },


    /**
     * whether or not noise suppression technology is enabled on an audio track
     * @type {Boolean}
     */
    noiseSuppression: {
      type: Boolean,
      required: false,
      default: true,
    },


    /**
     * Set a limit on the maximum record time
     * time is in milliseconds
     * default is 1 min
     */
    timeLimit: {
      type: Number,
      required: false,
      default: () => 100000,
    },


    /**
     * Provides media formats for the audio
     * @type {String}
     */
    mediaType: {
      type: String,
      required: false,
      default: 'audio/webm',
      validator: type => mediaTypes.map(x => x.mime).includes(type),
    },


    /**
     * Customized slots to pass ex:
     * :slots=[{name: 'snap', show: true, calss: '}]
     * @type {Array}
     * @todo check if passed props contain all slots
     */
    slots: {
      type: Array,
      required: false,
      default: () => [],
    },
  },


  data() {
    return {
      media: { recorder: null, chunk: [] },
    };
  },


  watch: {
    /**
     * If true, the browser will ask permission to access the microphone
     * @param {State}
     * @type {Boolean}
     */
    requestAccess(state) {
      if (state) {
        this.access();
      }
    },
  },


  methods: {
    /**
     * Check if browser supports mediaDevices API
     * and polyfil older browser support
     */
    checkBrowserSupport() {
      if (navigator.mediaDevices === undefined) {
        navigator.mediaDevices = {};
      }

      if (navigator.mediaDevices.getUserMedia === undefined) {
        navigator.mediaDevices.getUserMedia = function (constraints) {
          // First get ahold of the legacy getUserMedia, if present
          const getUserMedia = navigator.webkitGetUserMedia || navigator.mozGetUserMedia;

          // Some browsers just don't implement it - return a rejected promise with an error
          // to keep a consistent interface
          if (!getUserMedia) {
            return Promise.reject(new Error('getUserMedia is not implemented in this browser'));
          }

          // Otherwise, wrap the call to the old navigator.getUserMedia with a Promise
          return new Promise(((resolve, reject) => {
            getUserMedia.call(navigator, constraints, resolve, reject);
          }));
        };
      }
    },


    /**
     * The audio recorder method
     */
    async record() {
      const { bitRate, mimeType, media, timeLimit } = this;

      try {
        const stream = await navigator.mediaDevices.getUserMedia(
          this.mediaConstraints(),
        );

        media.recorder = new MediaRecorder(stream, {
          bitRate,
          mimeType,
        });

        media.recorder.start(10);

        media.recorder.addEventListener('dataavailable', (event) => {
          media.chunk.push(event.data);
        });

        setTimeout(() => {
          media.recorder.stop()
        }, timeLimit);

      } catch (err) {
        console.log('Recording error: ', err);
      }
    },


    /**
     * Stops the media and clears all stored stream data
     * This method is not required
     * @returns {void}
     */
    reset() {
      this.stop();
      this.media = { recorder: null, chunk: [] };
    },


    /**
     * Resume recording if state is paused
     * @returns {void}
     */
    resume() {
      var recorder = this.media.recorder

      if (recorder && recorder.state === 'paused') {
        recorder.resume();
      }
    },


    /**
     * Pause recording if state is recording
     * @returns {void}
     */
    pause() {
      var recorder = this.media.recorder

      if (recorder && recorder.state === 'recording') {
        recorder.pause();
      }
    },


    /**
     * Builds constraint for the userMedia API.
     * more options can be passed through :constraints prop
     * @returns {void}
     */
    mediaConstraints() {
      const {
        echoCancellation,
        noiseSuppression,
      } = this;

      return {
        audio: true,
        echoCancellation,
        noiseSuppression,
      };
    },


    /**
     * save the blob file of the recorded audio
     * @todo must write better code
     * @returns {void}
     * @todo check if ext: mp3 is supported, gotta read on the patent stuff
     */
    save() {
      const { media, mediaType } = this;

      if (!media.recorder) {
        return;
      }

      const blob = new Blob(media.chunk, {
        mediaType,
      });

      const link = document.createElement('a');
      link.href = window.URL.createObjectURL(blob);
      // todo: check ext and mp3 support
      link.download = `${this.name}.webm`;
      document.body.appendChild(link);
      link.click();
    },


    /**
     * Stops the recording of audio
     * Emits media:stop event
     * @returns {void}
     */
    stop() {
      var recorder  = this.media;

      if (recorder && recorder.state) {
        recorder.stop();
      }
    },


    /**
     * Executes a function based on user's selection.
     * @param {event} click event
     * @todo Must find a better way to get the clicked slot (button)
     */
    action(event) {
      // find alternative to get the clicked action (button)
      const action = event.path[0].attributes[1].nodeValue || '';

      // if permission is not yet accepted, avoid
      // calling the functions
      if (!this.requestAccess) {
        return;
      }

      switch (action) {
        case 'record':
          this.record();
          break;

        case 'pause':
          this.pause();
          break;

        case 'resume':
          this.resume();
          break;

        case 'stop':
          this.stop();
          break;

        case 'save':
          this.save();
          break;

        default:
          break;
      }
    },


    /**
     * Initiates the first request to access userMedia (microphone)
     * @returns {void}
     */
    access() {
      this.checkBrowserSupport();
    },
  },

  created() {},
};
</script>

